.nav-top {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    h1 {
        font-size: 80px;
        text-align: center;
        text-shadow: 0px 6px 2px $color-1;
        @media screen and (max-width: 1100px) {
            font-size: 50px;
        }
        @media screen and (max-width:600px) {
        font-size: 30px;
        }
    }
    nav {
        font-size: 30px;
        margin-left: 1%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        a {
            text-decoration: none;
            color: $white;
            &:hover {
                background-color: $color-1;
                border-radius: 50%;
            }
        }
        b {
            color: red;
            font-size: 20px;
        }
        @media screen and (max-width: 1100px) {
            font-size: 22px;
        }
        @media screen and (max-width: 700px) {
            flex-direction: column;
            align-items: flex-start;
            font-size: 18px;
            margin: 10px 0 0 10px;
            b {
                color: red;
                font-size: 10px;
        }
        }
    }
    .burger-icon {
        font-size: 40px;
        margin: 5px 0 0 10px;
        max-width: 20%;
        &:hover {
            cursor: pointer;
        }
    }
    .modal {
        display: flex;
        flex-direction: column;
        position: absolute;
        background-color: rgb(240, 239, 239, 0.9);
        border: 1px solid black;
        border-radius: 5px;
        max-width: 70%;
        z-index: 50;
        a {
            text-decoration: underline;
            text-align: center;
            font-size: 25px;
            color: $white;
            margin-left: 5px;
            text-shadow: 1px 2px 3px $black;
        }
    }
}