header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;

  form {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
  }

  input {
    width: 250px;
    text-align: center;
    font-size: 25px;
    border-radius: 10px 10px 0 0;
  }
  #search {
    border-radius: 0 0 10px 10px;
    background-color: $color-1;
    color: $white;
  }
  button, .top-flop {
    width: 250px;
    cursor: pointer;
    font-size: 25px;
  }
  .top-flop {
    display: flex;
    justify-content: center;

    button {
      cursor: pointer;
      width: 50%;
      background-color: $color-1;
      color: $white;
    }

    #top-btn {
      border-radius: 10px 0 0 10px;
    }
    #flop-btn {
      border-radius: 0 10px 10px 0;
    }
  }

}
